<template>
    
    <div id='startingWrapper'>

        <div class="videoHolder">

            <video :src="videoSource" class="videoAI" autoplay muted playsinline loop></video>

        </div>

        <div class="fadeBackgroundVideo"></div>

        <div class="enterGame">

            <h1>Fit Extreme</h1>

            <div @click="backgroundAudio(mediaPath)" id='startTheGameButton'>

                <router-link to="/game">Start</router-link>

            </div>

        </div>

    </div>

</template>


<script>

    import backgroundAudio from "../mixins/background.js";

    export default {

        data() {

            return {

                mediaPath : "sounds/fitGameBackgroundAudio.mp3"

            }

        },

        computed : {

            videoSource() {

                return require('@/assets/videos/dekuBackground.mp4');

            }

        },
        
        mixins : [backgroundAudio]


    }

</script>


<style lang="scss" scoped>

    $d1 : 1200px;
    $d2 : 576px;
    $d3 : 380px;


    @mixin d1 {

        @media (max-width: #{$d1}){

            @content;

        }

    }


    @mixin d2 {

        @media (max-width: #{$d2}){

            @content;

        }

    }


    @mixin d3 {

        @media (max-width: #{$d3}){

            @content;

        }

    }


    #startingWrapper {

        .enterGame {

            position: fixed;
            width : 80%;
            height : 30%;
            top : 35%;
            left : 10%;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;

            h1 {

                font-family: radi;
                font-size: 3.5em;
                letter-spacing: 5px;
                color : greenyellow;
                padding-bottom: 5%;
                text-align: center;

                @include d1 {

                    font-size : 2.8em;

                }

                @include d2 {

                    font-size : 2.6em;
                    padding-bottom: 10%;

                }

                @include d3 {

                    font-size: 2.3em;

                }

            }

            #startTheGameButton {

                font-family: radi;
                color : whitesmoke;
                background-color: yellowgreen;
                width : 35%;
                text-decoration: none;
                text-align: center;
                font-size: 1.4em;
                letter-spacing: 6px;
                transition: .4s;
                border-radius : 12px;

                @include d1 {

                    width : 60%;

                }

                @include d2 {

                    width : 100%;

                }

                a {

                    text-decoration: none;
                    display: block;
                    width : 100%;
                    color : whitesmoke;
                    padding : 5% 0%;

                }

                &:hover {

                    background-color : lighten(yellowgreen, 15%);

                }

            }

        }
        
        .fadeBackgroundVideo {

            position: fixed;
            top : 0;
            bottom : 0;
            left : 0;
            right : 0;
            background-color: rgba(0, 0, 0, .7);

        }

    }

</style>