export default {

    data() {

        return {

            playIt : ''

        }

    },

    methods : {

        backgroundAudio(mediaPath) {

            if(mediaPath === 'sounds/diceSound.wav') {

                const getBgAudio = require(`@/assets/${mediaPath}`);

                this.playIt = new Audio(getBgAudio);

                this.playIt.volume = 0.4;

                this.playIt.play();

            }

            if(localStorage.getItem('soundPlaying')) {

                return;

            }
            
            const getBgAudio = require(`@/assets/${mediaPath}`);

            this.playIt = new Audio(getBgAudio);

            localStorage.setItem('soundPlaying', true);

            this.playIt.volume = 0.4;

            this.playIt.play();

        }

    }

}