<template>
    
    <div id='theGameHolder'>

        <transition mode="out-in" name='fadeTutorial'>

            <div id="fadeTutorial" v-if="!endTutorial">

                <transition name="tutorialAnim">

                    <deku-tutorial v-if="dekuActive" @click="moveToDio"></deku-tutorial>

                </transition>

                <transition name="tutorialAnim">

                    <dio-tutorial v-if="dioActive" @click="moveToDioSecond"></dio-tutorial>

                </transition>

                <transition name="tutorialAnim">

                    <dio-tutorial-second v-if="dioSecondActive" @click="finishTutorial"></dio-tutorial-second>

                </transition>

                <transition name="tutorialAnim">

                    <go-back v-if="kaibaActive" @click="init"></go-back>

                </transition>

                <transition name="tutorialAnim">

                    <you-win v-if="gokuActive" @click="init"></you-win>

                </transition>

            </div>

        </transition>

        <!-- <div class="videoHolder">

            <video :src="videoSource" class="videoAI" autoplay muted playsinline loop></video>

        </div> -->

        <transition name='gameAnimation'>

            <div class="gameHolder" v-if="gameStarted">
            
                <div class="cardsHolder">

                    <div class="workoutCard">

                        <h4 ref="currentWorkout">Workout will appear here once you step on a dumbell field</h4>

                    </div>

                </div>

                <div class="gameTable">

                    <div class="gameBlock" v-for="field of fields" :key="field.id">

                        <h3 v-if="field.id == 0">Start</h3>

                        <h3 v-if="field.id == 30">End</h3>

                        <i v-if="field.id % 2 != 0 && field.id != 7 && field.id != 14 && field.id != 21 && field.id != 28" class="fas fa-dumbbell"></i>

                        <i v-if="field.id == 7 || field.id == 14 ||field.id == 21 ||field.id == 28" class="fas fa-skull-crossbones"></i>

                        <img id='avatarPlayer' src="../assets/images/superhero.svg" v-if="field.id === 0" alt='Starting character'/>

                    </div>

                </div>

                <div class="diceHolder">

                    <div class="workoutDice">

                        <div v-if="diceNumber == 1" class='diceNumber numberOneHolder'>

                            <div class="diceDot"></div>

                        </div>

                        <div v-if="diceNumber == 2" class='diceNumber numberTwoHolder'>

                            <div class="diceDot"></div>

                            <div class="diceDot"></div>

                        </div>

                        <div v-if="diceNumber == 3" class='diceNumber numberThreeHolder'>

                            <div class="diceDot"></div>

                            <div class="diceDot"></div>

                            <div class="diceDot"></div>

                        </div>

                    </div>

                    <button @click='rollDice' ref="rollDiceButton">Roll Dice</button>

                </div>

            </div>

        </transition>

    </div>

</template>


<script>

    import DekuTutorial from "./DekuTutorial.vue";

    import GoBack from "./GoBack.vue";

    import YouWin from "./YouWin.vue";

    import DioTutorial from "./DioTutorial.vue";

    import DioTutorialSecond from "./DioTutorialTwo.vue";

    import backgroundAudio from "../mixins/background.js";

    export default {

        components: { DekuTutorial, DioTutorial, DioTutorialSecond, GoBack, YouWin },

        mixins : [backgroundAudio],

        data() {

            return {

                dekuActive : false,
                dioActive : false,
                dioSecondActive : false,
                kaibaActive : false,
                gokuActive : false,
                endTutorial : false,
                diceNumber : 0,
                gameStarted : false,
                playerPosition : 0,
                mediaPath : "sounds/diceSound.wav",

                fields : [

                    {

                        "id" : 0,
                        "workout" : false

                    },

                    {

                        "id" : 1,
                        "workout" : true

                    },

                    {

                        "id" : 2,
                        "workout" : false

                    },

                    {

                        "id" : 3,
                        "workout" : true

                    },

                    {

                        "id" : 4,
                        "workout" : false

                    },

                    {

                        "id" : 5,
                        "workout" : true

                    },

                    {

                        "id" : 6,
                        "workout" : false

                    },

                    {

                        "id" : 7,
                        "workout" : true

                    },

                    {

                        "id" : 8,
                        "workout" : false

                    },

                    {

                        "id" : 9,
                        "workout" : true

                    },

                    {

                        "id" : 10,
                        "workout" : false

                    },

                    {

                        "id" : 11,
                        "workout" : true

                    },

                    {

                        "id" : 12,
                        "workout" : false

                    },

                    {

                        "id" : 13,
                        "workout" : true

                    },

                    {

                        "id" : 14,
                        "workout" : false

                    },

                    {

                        "id" : 15,
                        "goBack" : true

                    },

                    {

                        "id" : 16,
                        "workout" : false

                    },

                    {

                        "id" : 17,
                        "workout" : true

                    },

                    {

                        "id" : 18,
                        "workout" : false

                    },

                    {

                        "id" : 19,
                        "workout" : true

                    },

                    {

                        "id" : 20,
                        "workout" : false

                    },

                    {

                        "id" : 21,
                        "workout" : true

                    },

                    {

                        "id" : 22,
                        "workout" : false

                    },

                    {

                        "id" : 23,
                        "workout" : true

                    },

                    {

                        "id" : 24,
                        "workout" : false

                    },

                    {

                        "id" : 25,
                        "workout" : true

                    },

                    {

                        "id" : 26,
                        "workout" : false

                    },

                    {

                        "id" : 27,
                        "workout" : true

                    },

                    {

                        "id" : 28,
                        "workout" : false

                    },

                    {

                        "id" : 29,
                        "workout" : true

                    },

                    {

                        "id" : 30,
                        "workout" : false

                    },

                ],


                workouts : ['20 pushups', '30 seconds plank', '30 situps', '20 reps biceps curls', '20 reps shoulders', '15 reps triceps', '10 pushups', '15 squats', '60 seconds plank', '50 situps', '15 reps shoulders', '15 reps biceps curls', '25 seconds plank', '30 reps shoulders', '70 situps', '40 pushups', '40 reps biceps curls', '35 reps triceps', '80 seconds plank', '20 reps triceps']

            }

        },

        mounted() {

            setTimeout(() => {

                this.dekuActive = true;

            }, 1000);

        },

        computed : {

            videoSource() {

                return require('@/assets/videos/gameBackground.mp4');

            }

        },

         methods : {

             init() {

                 this.playerPosition = 0;

                 document.getElementById('avatarPlayer').style.top = '0px';

                 this.diceNumber = 0;

                 this.$refs.currentWorkout.innerHTML = "Workout will appear here once you step on a dumbell field";

                 this.kaibaActive = false;

                 this.gokuActive = false;

                 setTimeout(() => {

                     this.endTutorial = true;

                 }, 400);

                 document.body.scrollTop = 0; // For Safari

                 document.documentElement.scrollTop = 0; // For Chrome, Firefox, IE and Opera

             },

             rollDice() {

                 this.backgroundAudio(this.mediaPath);

                 this.$refs.rollDiceButton.setAttribute('disabled', 'true');

                 this.$refs.rollDiceButton.style.backgroundColor = '#c2c2c2';

                 setTimeout(() => {

                    this.$refs.rollDiceButton.removeAttribute('disabled');
                    
                    this.$refs.rollDiceButton.style.backgroundColor = 'greenyellow';

                 }, 1000);

                 let oneOrTwo = Math.ceil(Math.random() * 3);

                 let randomWorkoutNumber = Math.floor(Math.random() * this.workouts.length);

                 let currentOffsetTopValue = document.getElementById('avatarPlayer').offsetTop;

                 if(oneOrTwo === 1) {

                     this.diceNumber = 1;

                 }

                 else if(oneOrTwo === 2) {

                    this.diceNumber = 2;

                 }

                 else this.diceNumber = 3;

                 this.playerPosition += oneOrTwo;

                 if(this.playerPosition > 30) {

                     this.playerPosition -= oneOrTwo;

                     return;

                 }

                 for(let i = this.playerPosition; i < this.playerPosition + oneOrTwo ; i++) {

                     document.getElementById('avatarPlayer').style.top = currentOffsetTopValue + 124 + 'px';

                     currentOffsetTopValue += 124;

                 }

                 console.log(this.playerPosition)

                 if(this.playerPosition == 30) {

                     this.endTutorial = false;

                     this.$refs.currentWorkout.innerHTML = "Workout will appear here once you step on a dumbell field";

                     setTimeout(() => {

                         this.gokuActive = true;

                     }, 500);

                 }

                 if(this.playerPosition == 7 || this.playerPosition == 14 || this.playerPosition == 21 || this.playerPosition == 28) {

                     this.endTutorial = false;

                     this.$refs.currentWorkout.innerHTML = "Workout will appear here once you step on a dumbell field";

                     setTimeout(() => {

                         this.kaibaActive = true;

                     }, 500);

                 }

                 if(this.playerPosition % 2 != 0) {

                     this.$refs.currentWorkout.innerHTML = this.workouts[randomWorkoutNumber];

                 }

                 else this.$refs.currentWorkout.innerHTML = "Workout will appear here once you step on a dumbell field";
                 

             },

            moveToDio() {

                this.dekuActive = false;

                setTimeout(() => {

                    this.dioActive = true;

                }, 700);

            },

            moveToDioSecond() {

                this.dioActive = false;

                setTimeout(() => {

                    this.dioSecondActive = true;

                }, 700);

            },

            finishTutorial() {

                this.dioSecondActive = false;
                
                setTimeout(() => {

                    this.endTutorial = true;

                }, 700);


                setTimeout(() => {

                    this.gameStarted = true;

                }, 1500);

            }

        }
        

    }

</script>


<style scoped lang="scss">

    $d1 : 1200px;
    $d2 : 576px;
    $d3 : 380px;

    @mixin d1 {

        @media (max-width: #{$d1}){

            @content;

        }

    }

    @mixin d2 {

        @media (max-width: #{$d2}){

            @content;

        }

    }


    @mixin d3 {

        @media (max-width: #{$d3}){

            @content;

        }

    }

    #fadeTutorial {

        position: fixed;
        z-index: 1000;
        top : 0;
        left : 0;
        right : 0;
        bottom : 0;
        background-color: rgba(0, 0, 0, 0.3);
        display: flex;
        justify-content: center;
        align-items: center;

    }



    // Game table styling

        .gameHolder {

            position: relative;

            .diceHolder {

                position: fixed;
                width : 15%;
                height : 15%;
                top : 37%;
                right : 10%;

                @include d1 {

                    width : 30%;
                    right : 3%;

                }

                @include d2 {

                    right : 1%;
                    width : 28%;

                }

                p {

                    color : greenyellow;
                    font-weight: bold;
                    text-align: center;
                    font-size: 1.4em;
                    line-height: 30px;

                    @include d2 {

                        font-size: 1.1em;

                    }

                }

                button {

                    cursor : pointer;
                    outline: none;
                    display: flex;
                    margin: 15% auto;
                    width : 100%;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    line-height: 35px;
                    border-radius: 9px;
                    font-family: radi;
                    background-color: greenyellow;
                    color : #141414;
                    font-size : 1.2em;
                    border : none;
                    transition: .4s;

                    &:hover {

                        color: tomato !important;

                    }

                    @include d2 {

                        margin : 25% auto;

                    }

                }

                .workoutDice {

                    width : 100%;
                    height : 100%;
                    background-color: lighten(#141414, 5%);
                    border-radius: 12px;


                    .diceNumber {

                        width : 100%;

                        .diceDot {

                            width : 14px;
                            height : 14px;
                            border-radius: 50%;
                            background-color: greenyellow;

                        }

                    }

                    .numberOneHolder {

                        display: flex;
                        justify-content: center;
                        align-items: center;
                        height : 100%;

                    }

                    .numberTwoHolder {

                        display: flex;
                        justify-content: center;
                        align-items: center;
                        height : 100%;


                        .diceDot {

                            margin : 0% 6%;

                        }

                    }

                    .numberThreeHolder {

                        display: flex;
                        justify-content: space-around;
                        align-items: center;
                        height : 100%;

                    }

                }

            }

            .cardsHolder {

                position: fixed;
                width : 20%;
                height : 60%;
                top : 20%;
                left : 10%;

                @include d1{

                    width : 30%;
                    left : 3%;

                }

                @include d2 {

                    left : 1%;

                }

                .workoutCard {

                    width : 100%;
                    height : 100%;
                    background-color: lighten(#141414, 4%);
                    border-radius: 12px;
                    display: flex;
                    justify-content: center;
                    align-items: center;


                    h4 {

                        text-align: center;
                        padding : 0% 15%;
                        letter-spacing: 2.5px;
                        line-height: 35px;
                        color : greenyellow;
                        font-size: 1.7em;

                        @include d2 {

                            font-size: 1.3em;

                        }

                        @include d1 {

                            font-size : 1.1em;

                        }

                    }

                }

            }

            .gameTable {

                max-width: 100%;
                padding : 4%;
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                flex-wrap: wrap;


                .gameBlock {

                    width : 120px;
                    height : 120px;
                    background-color: #141414;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    position: relative;

                    img {

                        position: absolute;
                        z-index: 999;
                        height : 40%;
                        transition : 0.6s;
                        top : 0px;

                        // Magic number 124px

                    }

                    h3 {

                        color : whitesmoke;
                        font-family: radi;

                    }

                    i {

                        color : tomato;
                        font-size: 1.8em;

                    }

                    &:nth-child(odd) {

                        border : 2px solid greenyellow;

                    }

                    &:nth-child(even) {

                        border : 2px solid yellow;

                    }

                    &:first-child {

                        background-color: lighten(#141414, 10%);

                    }

                    &:last-child {

                        background-color: lighten(#141414, 10%);

                    }

                }

            }

        }


    .tutorialAnim-enter-from,
    .tutorialAnim-leave-to {

        opacity : 0;
        transform: translateX(30px);

    }


    .tutorialAnim-enter-active {

        transition : all 0.4s ease-out;

    }

    .tutorialAnim-leave-active {

        transition : all 0.4s ease-in;

    }

    .tutorialAnim-enter-to,
    .tutorialAnim-leave-from {

        opacity : 1;
        transform: translateX(0px);

    }


    .fadeTutorial-leave-from,
    .fadeTutorial-enter-to {

        opacity : 1;

    }


    .fadeTutorial-enter-active {

        transition: opacity .4s ease-out;

    }

    
    .fadeTutorial-leave-active {

        transition: opacity .4s ease-in;

    }


    .fadeTutorial-leave-to,
    .fadeTutorial-enter-from {

        opacity : 0;

    }
    

    .gameAnimation-enter-from {

        opacity : 0;

    }


    .gameAnimation-enter-active {

        transition : all .4s ease-out;

    }

    .gameAnimation-enter-to {

        opacity : 1;

    }


</style>